* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: url("11b6769c8f2ea91b25fa0bb10bcaf4ab.jpg");
  background-size: cover;
  background-attachment: fixed;
  font-family: "Montserrat", sans-serif;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
.header {
  font-size: 50px;
  text-align: center;
  color: white;
  text-shadow: 1px 1px 10px #000000;
  padding: 50px;
}
.container {
  box-sizing: content-box;
  border: 4px solid black;
  border-right: 2px solid black;
  margin: auto;
  width: 540px;
  height: 540px;
  display: grid;
  grid-template: repeat(9, 60px) / repeat(9, 60px);
}
.element {
  width: 60px;
  height: 60px;
  background-color: lightgrey;
  border: 1px solid black;
  text-align: center;
  line-height: 60px;
  font-size: 40px;
  cursor: pointer;
}
.element:nth-child(3n) {
  border-right: 3px solid black;
}
.element[data-y="2"],
.element[data-y="5"] {
  border-bottom: 3px solid black;
}
.digits {
  background-color: lightgrey;
  width: 270px;
  height: 270px;
  display: grid;
  grid-template: repeat(3, 90px) / repeat(3, 90px);
  grid-gap: 5px;
  position: absolute;
  right: 10%;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
}
.digit {
  border-radius: 4px;
  border: 2px solid black;
  text-align: center;
  font-size: 45px;
  line-height: 40px;
  cursor: pointer;
}
.digit:hover {
  transform: scale(1.05);
}
.digit:focus {
  outline: none;
}
.selected {
  background-color: black;
  border: none;
  color: white;
}
.legend {
  position: absolute;
  right: 13%;
  top: 30%;
  transform: translateY(-50%);
  color: white;
  text-shadow: 1px 1px 10px #000000;
}
.digit_complete {
  color: grey;
  border: none;
  background-color: rgb(226, 226, 226);
}
.loader {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}
.loader {
  border: 8px solid #f3f3f3;
  border-radius: 50%;
  border-top: 8px solid black;
  width: 60px;
  height: 60px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@media only screen and (max-width: 600px) {
  .header {
    font-size: 32px;
    padding: 50px 20px;
  }
  .container {
    border: 2px solid black;
    border-right: 1px solid black;
    width: 90vw;
    height: 90vw;
    grid-template: repeat(9, 10vw) / repeat(9, 10vw);
  }
  .element {
    width: 10vw;
    height: 10vw;
    border: 1px solid black;
    line-height: 10vw;
    font-size: 26px;
  }
  .element:nth-child(3n) {
    border-right: 2px solid black;
  }
  .element[data-y="2"],
  .element[data-y="5"] {
    border-bottom: 2px solid black;
  }
  .digits {
    width: 94.5vw;
    height: 15vw;
    grid-template: 15vw / repeat(9, 10.5vw);
    grid-gap: 1px;
    right: 49%;
    left: 49%;
    top: 83%;
    bottom: 17%;
    transform: translate(-50%, -50%);
  }
  .digit {
    border-radius: 3px;
    border: 1px solid black;
    line-height: 15vw;
  }
  .digit_complete {
    color: grey;
    border: none;
    background-color: rgb(226, 226, 226);
  }
  .legend {
    right: 0%;
    left: 2%;
    top: 76%;
  }
  .loader {
    border: 4px solid #f3f3f3;
    border-radius: 50%;
    border-top: 4px solid black;
    width: 10vw;
    height: 10vw;
    animation: spin 1s linear infinite;
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}
